import { useDispatch } from 'react-redux';
import { Icons } from '../../types/enums/Icons';
import { Icon } from '../Icon';
import './Searchbar.scss';
import { productsSlice } from '../../store/reducers/ProductsSlice';
import { useAppSelector } from '../../store/hooks/redux';

interface Props {
  placeholder?: string,
}

export const Searchbar: React.FC<Props> = ({ placeholder }) => {
  const dispatch = useDispatch();

  const onChange = (text: string) => {
    dispatch(productsSlice.actions.setSearchbar(text));
  };

  const {
    searchbar,
  } = useAppSelector(state => state.productsReducer);

  return (
    <div className="searchbar">
      <input
        className="searchbar__input"
        type="text"
        placeholder={`Search in ${placeholder}`}
        onChange={e => onChange(e.target.value)}
        value={searchbar}
      />
      <div className="searchbar__icon">
        <Icon icon={Icons.Search} />
      </div>
    </div>
  );
};
